
const contains=function(arr,obj,attr) {
    if(attr){
        var i = arr.length;
        while (i--) {
            if ( arr[i][attr]=== obj) {
                return i;
            }
        }
        return "false";
    }else{
        var i = arr.length;
        while (i--) {
            if ( arr[i]=== obj) {
                return i;
            }
        }
        return "false";
    }
};
let getTimes=function(nS,type) {
    if(typeof nS == 'string'){
        var date=new Date(parseInt(nS));
    }else if(typeof nS == 'object'){
        var date=new Date(nS);
    }else{
        var date=new Date(nS);
    }
    var year=date.getFullYear();
    var mon = date.getMonth()+1;
    if(mon<10){
        mon="0"+mon;
    }
    var day = date.getDate();
    if(day<10){
        day="0"+day;
    }
    var hours = date.getHours();
    if(hours<10){
        hours="0"+hours;
    }
    var minu = date.getMinutes();
    if(minu<10){
        minu="0"+minu;
    }
    var sec = date.getSeconds();
    if(sec<10){
        sec="0"+sec;
    }
    if(type==''||type==undefined||type==null||type=="YY-MM-DD hh:mm:ss"){
        return year+'-'+mon+'-'+day+' '+hours+':'+minu+':'+sec;
    }else if(type=="YY-MM-DD"){
        return year+'-'+mon+'-'+day;
    }else if(type=="YY-MM-DD hh:mm"){
        return year+'-'+mon+'-'+day+' '+hours+':'+minu;
    }
}
let reverseFn=function(arr){
    let newArr=[];
    for(let item of arr){
        newArr.unshift(item);
    }
    return newArr;
}
let siblings=function(elem){ //elem 
    var nodes=[]; //elem 
    var previ=elem.previousSibling; 
    while(previ){ //o  previpreviousSibling 
        if(previ.nodeType===1){ 
            nodes.push(previ); 
        } 
        previ=previ.previousSibling; //previ 
    } 
    nodes.reverse(); //  
    var nexts=elem.nextSibling; //elem 
    while(nexts){ // nextsnextSibling 
        if(nexts.nodeType===1){ 
            nodes.push(nexts); 
        } 
        nexts=nexts.nextSibling; 
    } 
    return nodes; // 
} 
export default{
    contains,
    siblings,
    getTimes,
    reverseFn
}


